$colorOne  : #F25C84;
$colorTwo  : #9C4EA6;
$colorThree: #F2DA63;

@mixin colorPalette ($color) {}

body {
  margin    : 0.4rem 2rem;
  box-sizing: content-box;
}

.wrapper {
  display       : flex;
  flex-direction: column;
  gap: 1.5rem;
}

.container {
  display: flex;
  gap    : 1rem;

  &__box {
    display         : flex;
    justify-content : center;
    align-items     : center;
    width           : 8rem;
    height          : 5rem;
    border-radius   : 0.8em;
    background-color: $colorOne;
    color           : white;
  }
    :nth-child(1) {
      background-color: darken($colorOne, 50%);
    }

    :nth-child(2) {
      background-color: darken($colorOne, 35%);
    }

    :nth-child(3) {
      background-color: darken($colorOne, 20%);
    }

    :nth-child(4) {
      background-color: darken($colorOne, 15%);
    }

    :nth-last-child(4) {
      background-color: lighten($colorOne, 8%);
    }

    :nth-last-child(3) {
      background-color: lighten($colorOne, 14%);
    }

    :nth-last-child(2) {
      background-color: lighten($colorOne, 18%);
    }

    :nth-last-child(1) {
      background-color: lighten($colorOne, 20%);
    }

    :nth-last-child(-n+4) {
      color: black;
    }
  
}